<template>
  <div class="color-primary p-2">
    <div v-if="tokens.length > 0">
      <div class="card card-default">
        <div class="card-header text-dark">
          <h3>授權列表</h3>
        </div>

        <div class="card-body">
          <!-- Authorized Tokens -->
          <table class="table table-borderless mb-0">
            <thead>
              <tr>
                <th>名稱</th>
                <th>範圍(Scopes)</th>
                <th>撤銷</th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="token in tokens" :key="token.client.id">
                <!-- Client Name -->
                <td style="vertical-align: middle;">{{ token.client.name }}</td>

                <!-- Scopes -->
                <td style="vertical-align: middle;">
                  <span v-if="token.scopes.length > 0">{{ token.scopes.join(', ') }}</span>
                </td>

                <!-- Revoke Button -->
                <td style="vertical-align: middle;">
                  <a class="action-link text-danger" @click="revoke(token)">撤銷</a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  /*
   * The component's data.
   */
  data() {
    return {
      tokens: []
    };
  },

  /**
   * Prepare the component (Vue 1.x).
   */
  ready() {
    this.prepareComponent();
  },

  /**
   * Prepare the component (Vue 2.x).
   */
  mounted() {
    this.prepareComponent();
  },

  methods: {
    /**
     * Prepare the component (Vue 2.x).
     */
    prepareComponent() {
      this.getTokens();
    },

    /**
     * Get all of the authorized tokens for the user.
     */
    getTokens() {
      axios.get("/oauth/tokens").then(response => {
        this.tokens = response.data;
      });
    },

    /**
     * Revoke the given token.
     */
    revoke(token) {
      axios.delete("/oauth/tokens/" + token.id).then(response => {
        this.getTokens();
      });
    }
  }
};
</script>

<style scoped>
.action-link {
  cursor: pointer;
}
</style>
