<template>
  <div class="color-primary p-2">
    <div>
      <div class="card card-default">
        <div class="card-header">
          <div style="display: flex; justify-content: space-between; align-items: center;">
            <h3>
              <span>個人憑證 Access Tokens</span>
            </h3>

            <a
              class="action-link btn btn-info text-white"
              tabindex="-1"
              @click="showCreateTokenForm"
            >建立新的個人憑證</a>
          </div>
        </div>

        <div class="card-body">
          <!-- No Tokens Notice -->
          <p class="mb-0" v-if="tokens.length === 0">您尚未建立任何個人憑證。</p>

          <!-- Personal Access Tokens -->
          <table class="table table-borderless mb-0" v-if="tokens.length > 0">
            <thead>
              <tr>
                <th>名稱</th>
                <th></th>
              </tr>
            </thead>

            <tbody>
              <tr v-for="token in tokens" :key="token">
                <!-- Client Name -->
                <td style="vertical-align: middle;">{{ token.name }}</td>

                <!-- Delete Button -->
                <td style="vertical-align: middle;">
                  <a class="action-link text-danger" @click="revoke(token)">刪除</a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!-- Create Token Modal -->
    <div class="modal fade" id="modal-create-token" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">建立新的個人憑證</h4>

            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
          </div>

          <div class="modal-body">
            <!-- Form Errors -->
            <div class="alert alert-danger" v-if="form.errors.length > 0">
              <p class="mb-0">
                <strong>噢哦！</strong> 發生了一些錯誤！
              </p>
              <br />
              <ul>
                <li v-for="error in form.errors" :key="error">{{ error }}</li>
              </ul>
            </div>

            <!-- Create Token Form -->
            <form role="form" @submit.prevent="store">
              <!-- Name -->
              <div class="form-group row">
                <label class="col-md-4 col-form-label">名稱</label>

                <div class="col-md-6">
                  <input
                    id="create-token-name"
                    type="text"
                    class="form-control"
                    name="name"
                    v-model="form.name"
                  />
                </div>
              </div>

              <!-- Scopes -->
              <div class="form-group row" v-if="scopes.length > 0">
                <label class="col-md-4 col-form-label">權限</label>

                <div class="col-md-6">
                  <div v-for="scope in scopes" :key="scope.id">
                    <div class="checkbox">
                      <label>
                        <input
                          type="checkbox"
                          @click="toggleScope(scope.id)"
                          :checked="scopeIsAssigned(scope.id)"
                        />
                        {{ scope.id }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>

          <!-- Modal Actions -->
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>

            <button type="button" class="btn btn-primary" @click="store">新增</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Access Token Modal -->
    <div class="modal fade" id="modal-access-token" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">個人憑證 Access Token</h4>

            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
          </div>

          <div class="modal-body">
            <p>這是您新的個人憑證（Access token），他只會顯示在你面前一次，也就是本次，所以請不要弄丟了！您現在可以使用此憑證發出 API 請求。</p>

            <textarea class="form-control" rows="10" :value="accessToken"></textarea>
          </div>

          <!-- Modal Actions -->
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  /*
   * The component's data.
   */
  data() {
    return {
      accessToken: null,

      tokens: [],
      scopes: [],

      form: {
        name: "",
        scopes: [],
        errors: []
      }
    };
  },

  /**
   * Prepare the component (Vue 1.x).
   */
  ready() {
    this.prepareComponent();
  },

  /**
   * Prepare the component (Vue 2.x).
   */
  mounted() {
    this.prepareComponent();
  },

  methods: {
    /**
     * Prepare the component.
     */
    prepareComponent() {
      this.getTokens();
      this.getScopes();

      $("#modal-create-token").on("shown.bs.modal", () => {
        $("#create-token-name").focus();
      });
    },

    /**
     * Get all of the personal access tokens for the user.
     */
    getTokens() {
      axios.get("/oauth/personal-access-tokens").then(response => {
        this.tokens = response.data;
      });
    },

    /**
     * Get all of the available scopes.
     */
    getScopes() {
      axios.get("/oauth/scopes").then(response => {
        this.scopes = response.data;
      });
    },

    /**
     * Show the form for creating new tokens.
     */
    showCreateTokenForm() {
      $("#modal-create-token").modal("show");
      $(".modal-backdrop").hide();
    },

    /**
     * Create a new personal access token.
     */
    store() {
      this.accessToken = null;

      this.form.errors = [];

      axios
        .post("/oauth/personal-access-tokens", this.form)
        .then(response => {
          this.form.name = "";
          this.form.scopes = [];
          this.form.errors = [];

          this.tokens.push(response.data.token);

          this.showAccessToken(response.data.accessToken);
        })
        .catch(error => {
          if (typeof error.response.data === "object") {
            this.form.errors = _.flatten(_.toArray(error.response.data.errors));
          } else {
            this.form.errors = ["出了一些問題，請再重新嘗試一遍看看。"];
          }
        });
    },

    /**
     * Toggle the given scope in the list of assigned scopes.
     */
    toggleScope(scope) {
      if (this.scopeIsAssigned(scope)) {
        this.form.scopes = _.reject(this.form.scopes, s => s == scope);
      } else {
        this.form.scopes.push(scope);
      }
    },

    /**
     * Determine if the given scope has been assigned to the token.
     */
    scopeIsAssigned(scope) {
      return _.indexOf(this.form.scopes, scope) >= 0;
    },

    /**
     * Show the given access token to the user.
     */

    showAccessToken(accessToken) {
      $("#modal-create-token").modal("hide");

      this.accessToken = accessToken;

      $("#modal-access-token").modal("show");
      $(".modal-backdrop").hide();
    },

    /**
     * Revoke the given token.
     */
    revoke(token) {
      axios
        .delete("/oauth/personal-access-tokens/" + token.id)
        .then(response => {
          this.getTokens();
        });
    }
  }
};
</script>

<style scoped>
.action-link {
  cursor: pointer;
}
</style>
